ground-color: #f0f9ff;\n  box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\n  animation: pulse-42c7142d 2s ease-in-out infinite;\n}\n@keyframes pulse-42c7142d {\n0% {\n    box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\n}\n50% {\n    box-shadow: 0 4px 16px rgba(24, 144, 255, 0.2);\n}\n100% {\n    box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\n}\n}\n.request-info[data-v-42c7142d] {\n  margin-bottom: 8px;\n}\n.request-url[data-v-42c7142d] {\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\n  font-size: 12px;\n  color: #333;\n  word-break: break-all;\n  margin-bottom: 4px;\n}\n.request-meta[data-v-42c7142d] {\n  display: flex;\n  gap: 12px;\n  font-size: 11px;\n}\n.method[data-v-42c7142d] {\n  background: #1890ff;\n  color: white;\n  padding: 2px 6px;\n  border-radius: 2px;\n  font-weight: bold;\n}\n.status[data-v-42c7142d] {\n  padding: 2px 6px;\n  border-radius: 2px;\n  font-weight: bold;\n}\n.status.pending[data-v-42c7142d] {\n  background: #faad14;\n  color: white;\n}\n.status.completed[data-v-42c7142d] {\n  background: #52c41a;\n  color: white;\n}\n.status.parsing[data-v-42c7142d] {\n  background: #1890ff;\n  color: white;\n}\n.status.parsing .el-icon-loading[data-v-42c7142d] {\n  animation: rotating-42c7142d 2s linear infinite;\n}\n@keyframes rotating-42c7142d {\n0% {\n    transform: rotate(0deg);\n}\n100% {\n    transform: rotate(360deg);\n}\n}\n.status.failed[data-v-42c7142d] {\n  background: #ff4d4f;\n  color: white;\n}\n.timestamp[data-v-42c7142d] {\n  color: #999;\n}\n.response-data[data-v-42c7142d] {\n  margin-top: 8px;\n  padding-top: 8px;\n  border-top: 1px solid #f0f0f0;\n}\n.response-content[data-v-42c7142d] {\n  margin-top: 8px;\n  background: #f8f8f8;\n  border-radius: 4px;\n  padding: 8px;\n  max-height: 300px;\n  overflow-y: auto;\n}\n.response-content pre[data-v-42c7142d] {\n  margin: 0;\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\n  font-size: 11px;\n  line-height: 1.4;\n  color: #333;\n  white-space: pre-wrap;\n  word-break: break-word;\n}\n.role-card[data-v-42c7142d] .el-card__body {\n  padding: 8px;\n}\n\n/*  */\n.role-card.empty-role[data-v-42c7142d] {\n  opacity: 0.6;\n  filter: grayscale(0.8);\n  background-color: #f5f5f5;\n  border: 1px solid #d9d9d9;\n  transition: all 0.3s ease;\n}\n.role-card.empty-role[data-v-42c7142d]:hover {\n  opacity: 0.8;\n  filter: grayscale(0.6);\n}\n.role-card.empty-role[data-v-42c7142d] .el-card__body {\n  background-color: #fafafa;\n}\n\n/*  */\n.role-card.empty-role .el-tag[data-v-42c7142d] {\n  opacity: 0.7;\n}\n.role-card.empty-role .el-link[data-v-42c7142d] {\n  opacity: 0.7;\n}\n.role-card.empty-role span[data-v-42c7142d] {\n  opacity: 0.7;\n}\n", "",{"version":3,"sources":["webpack://./src/chrome-extensions/DevToolsPanel.vue"],"names":[],"mappings":";AA++BA;EACA,sBAAA;EACA,aAAA;EACA,gFAAA;EACA,mBAAA;EACA,iBAAA;EACA,4DAAA;EACA,YAAA;EACA,cAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,mBAAA;EACA,oBAAA;EACA,gCAAA;AACA;AAEA;EACA,SAAA;EACA,WAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,SAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,QAAA;AACA;AAEA;EACA,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EACA,UAAA;EACA,WAAA;EACA,kBAAA;EACA,qBAAA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,oDAAA;AACA;AAEA;EACA,yBAAA;EACA,mDAAA;AACA;;AAEA,aAAA;AACA;AACA;IACA,yCAAA;IACA,0CAAA;IACA,UAAA;AACA;AAEA;IACA,2CAAA;IACA,2CAAA;IACA,YAAA;AACA;AAEA;IACA,yCAAA;IACA,0CAAA;IACA,UAAA;AACA;AACA;;AAEA,aAAA;AACA;AACA;IACA,yCAAA;IACA,2CAAA;IACA,UAAA;AACA;AAEA;IACA,2CAAA;IACA,6CAAA;IACA,YAAA;AACA;AAEA;IACA,2CAAA;IACA,4CAAA;IACA,YAAA;AACA;AAEA;IACA,2CAAA;IACA,6CAAA;IACA,YAAA;AACA;AAEA;IACA,yCAAA;IACA,2CAAA;IACA,UAAA;AACA;AACA;AAEA;EACA,eAAA;EACA,WAAA;EACA,gBAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,WAAA;EACA,eAAA;AACA;AAEA;EACA,kBAAA;EACA,kBAAA;EACA,WAAA;EACA,iBAAA;EACA,kBAAA;EACA,uBAAA;AACA;AAEA;EACA,iBAAA;EACA,kBAAA;EACA,yBAAA;EACA,gBAAA;AACA;AAEA;EACA,gCAAA;EACA,kBAAA;EACA,iCAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,6CAAA;EACA,iDAAA;AACA;AAEA;AACA;IACA,6CAAA;AACA;AAEA;IACA,8CAAA;AACA;AAEA;IACA,6CAAA;AACA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,wDAAA;EACA,eAAA;EACA,WAAA;EACA,qBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;AACA;AAEA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,+CAAA;AACA;AAEA;AACA;IACA,uBAAA;AACA;AAEA;IACA,yBAAA;AACA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,WAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,6BAAA;AACA;AAEA;EACA,eAAA;EACA,mBAAA;EACA,kBAAA;EACA,YAAA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,SAAA;EACA,wDAAA;EACA,eAAA;EACA,gBAAA;EACA,WAAA;EACA,qBAAA;EACA,sBAAA;AACA;AAEA;EACA,YAAA;AACA;;AAEA,aAAA;AACA;EACA,YAAA;EACA,sBAAA;EACA,yBAAA;EACA,yBAAA;EACA,yBAAA;AACA;AAEA;EACA,YAAA;EACA,sBAAA;AACA;AAEA;EACA,yBAAA;AACA;;AAEA,gBAAA;AACA;EACA,YAAA;AACA;AAEA;EACA,YAAA;AACA;AAEA;EACA,YAAA;AACA","sourcesContent":["<template>\n  <div class=\"panel\">\n    <div class=\"panel-header\">\n      <el-row type=\"flex\" align=\"middle\">\n        <div style=\"width: 32px;height: 32px;margin-right: 10px;position: relative;\">\n          <img src=\"~@/assets/logo.png\" alt=\"\" style=\"width: 32px;height: 32px;\">\n          <span class=\"status-dot\"\n            :class=\"{ 'connected': devtoolsConnected, 'disconnected': !devtoolsConnected }\"></span>\n        </div>\n        <h3 style=\"color: #fff;\"></h3>\n      </el-row>\n      <div class=\"connection-status\">\n        <div id=\"pager\" class=\"fr\" v-if=\"pageInfo.hasPager\">\n          <el-row class=\"pages\" type=\"flex\" align=\"middle\">\n            <span style=\"color: #fff;margin-right: 10px;\"> {{ pageInfo.currentPage }}, {{ pageInfo.total }} </span>\n            <a v-if=\"pageInfo.hasPrev\" href=\"javascript:void 0;\" @click.prevent=\"prevPage\"\n              style=\"line-height: 1.2em;\"></a>\n            <a v-if=\"pageInfo.hasNext\" href=\"javascript:void 0;\" @click.prevent=\"nextPage\"\n              style=\"line-height: 1.2em;\"></a>\n          </el-row>\n        </div>\n        <a v-if=\"!devtoolsConnected\" href=\"javascript:void 0;\" @click=\"reconnectDevTools\"></a>\n        <a v-if=\"!isInNewWindow\" href=\"javascript:void 0;\" class=\" btn1 js_alert_btn_0\"\n          @click.prevent=\"openInNewTab\"></a>\n        <a v-if=\"!pageInfo.hasPager\" href=\"javascript:void 0;\" class=\" btn1 js_alert_btn_0\"\n          @click.prevent=\"refreshCurrentPage\"></a>\n        <a v-if=\"recommendData.length > 0\" href=\"javascript:void 0;\" class=\" btn1 js_alert_btn_0\"\n          @click.prevent=\"clearData\"></a>\n      </div>\n    </div>\n    <div class=\"data-section\">\n      <el-empty v-if=\"recommendData.length === 0\" class=\"empty-state\" description=\"\"></el-empty>\n      <div v-else class=\"request-list\">\n        <div v-for=\"(item, index) in recommendData\" :key=\"item.requestId\" class=\"request-item\"\n          :class=\"{ 'parsing': item.status === 'parsing' }\">\n          <div class=\"request-info\">\n            <div class=\"request-meta\">\n              <span class=\"status\" :class=\"item.status\">\n                <template v-if=\"item.status === 'parsing'\">\n                  <i class=\"el-icon-loading\"></i> ...\n                </template>\n                <template v-else-if=\"item.status === 'completed'\">\n                  <i class=\"el-icon-success\"></i> \n                </template>\n                <template v-else>\n                  <i class=\"el-icon-error\"></i> \n                </template>\n              </span>\n              <span class=\"timestamp\">{{ formatTime(item.timestamp) }}</span>\n            </div>\n          </div>\n          <div v-if=\"item.responseData\" class=\"response-data\">\n            <el-row :gutter=\"4\">\n              <el-col v-for=\"role in parseListData(item.responseData)?.equip_list\" :key=\"role.eid\"\n                style=\"width: 20%;margin-bottom: 2px;margin-top: 2px;\">\n                <el-card class=\"role-card\" :class=\"{ 'empty-role': isEmptyRole(parserRoleData(role)) }\">\n                  <el-row type=\"flex\" justify=\"space-between\">\n                    <el-col style=\"width:50px;flex-shrink: 0;margin-right: 4px;\">\n                      <RoleImage :key=\"role.eid\" :other_info=\"role.other_info\" :roleInfo=\"parserRoleData(role)\" />\n                      <el-link :href=\"getCBGLinkByType(role.eid, 'role')\" type=\"danger\" target=\"_blank\"\n                        style=\"white-space: nowrap;text-overflow: ellipsis;overflow: hidden;display: block;font-size: 12px;\">\n                        {{ role.seller_nickname }}</el-link>\n                    </el-col>\n                    <el-col>\n                      <div>\n                        <el-tag type=\"success\" v-if=\"role.accept_bargain == 1\"></el-tag>\n                        <el-tag type=\"danger\" v-else></el-tag>\n                      </div>\n                      <div style=\"padding: 5px 0;\">\n                        <span v-html=\"formatFullPrice(role.price, true)\"></span>\n                      </div>\n                      <div>\n                        <el-tag type=\"danger\" v-if=\"isEmptyRole(parserRoleData(role))\"></el-tag>\n                        <template v-else>\n                          <el-tag @click=\"handleEquipPrice(role)\" style=\"cursor: pointer;\" v-if=\"get_equip_num(parserRoleData(role)) > 0\">\n                             {{ get_equip_num(parserRoleData(role)) }}\n                          </el-tag>\n                          <el-tag type=\"success\" @click=\"handlePetPrice(role)\" style=\"cursor: pointer;\" v-if=\"get_pet_num(parserRoleData(role)) > 0\">\n                             {{ get_pet_num(parserRoleData(role)) }}\n                          </el-tag>\n                        </template>\n                      </div>\n\n                    </el-col>\n                  </el-row>\n                  <div>\n                    <SimilarRoleModal :role=\"{ ...role, roleInfo: parserRoleData(role) }\"\n                      :search-params=\"{ selectedDate: selectedDate, roleType: 'normal' }\">\n                      <div> <el-link type=\"primary\" href=\"javascript:void 0;\" @click.prevent\n                          :disabled=\"item.status !== 'completed'\">\n                          </el-link></div>\n                    </SimilarRoleModal>\n                  </div>\n                </el-card>\n              </el-col>\n            </el-row>\n            <!-- <el-button @click=\"toggleResponse(index)\" size=\"mini\" type=\"text\">\n              {{ expandedItems.includes(index) ? '' : '' }}\n            </el-button>\n            <div v-if=\"expandedItems.includes(index)\" class=\"response-content\">\n              <pre>{{ JSON.stringify(item.responseData, null, 2) }}</pre>\n            </div> -->\n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <el-dialog :visible.sync=\"valuationDialogVisible\" width=\"1000px\" :close-on-click-modal=\"false\"\n      :close-on-press-escape=\"false\" custom-class=\"batch-valuation-dialog\">\n      <span slot=\"title\" class=\"el-dialog__title\">\n        <el-tag size=\"mini\">{{ valuationDialogTitle.server_name }}</el-tag>\n        /\n        <el-tag type=\"info\" size=\"mini\">{{ valuationDialogTitle.school }}</el-tag>/\n        <el-link :href=\"getCBGLinkByType(valuationDialogTitle.eid)\" target=\"_blank\">{{ valuationDialogTitle.nickname\n        }}</el-link>\n      </span>\n      <EquipBatchValuationResult :results=\"valuationResults\" :total-value=\"valuationTotalValue\"\n        :equipment-list=\"valuationEquipmentList\" :valuate-params=\"batchValuateParams\" :loading=\"valuationLoading\"\n        @close=\"closeValuationDialog\" />\n    </el-dialog>\n  </div>\n</template>\n<script>\nimport dayjs from 'dayjs'\nimport RoleImage from '@/components/RoleInfo/RoleImage.vue'\nimport SimilarRoleModal from '@/components/SimilarRoleModal.vue'\nimport EquipBatchValuationResult from '@/components/EquipBatchValuationResult.vue'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\nexport default {\n  name: 'DevToolsPanel',\n  data() {\n    return {\n      pageInfo: {\n        hasPager: false,\n        currentPage: 0,\n        total: 0,\n        hasPrev: false,\n        hasNext: false\n      },\n      selectedDate: dayjs().format('YYYY-MM'),\n      recommendData: [],\n      expandedItems: [],\n      processedRequests: new Set(), // ID\n      devtoolsConnected: false, // \n      connectionStatus: '...', // \n      connectionCheckTimer: null, // \n      isInNewWindow: false, // \n      \n      // \n      valuationDialogVisible: false,\n      valuationResults: [],\n      valuationTotalValue: 0,\n      valuationEquipmentList: [],\n      valuationLoading: false,\n      valuationDialogTitle: {},\n      batchValuateParams: {\n        similarity_threshold: 0.7,\n        max_anchors: 30\n      }\n    }\n  },\n  mixins: [commonMixin],\n  components: {\n    RoleImage,\n    SimilarRoleModal,\n    EquipBatchValuationResult\n  },\n  computed: {\n\n  },\n  mounted() {\n    // background script\n    if (typeof chrome !== 'undefined' && chrome.runtime) {\n      chrome.runtime.sendMessage({\n        action: 'sidePanelOpened'\n      })\n    }\n\n    // \n    document.addEventListener('visibilitychange', this.handleVisibilityChange)\n\n    this.initMessageListener()\n    this.checkConnectionStatus()\n    this.checkIfInNewWindow()\n\n    // // 5\n    // this.connectionCheckTimer = setInterval(() => {\n    //   this.checkConnectionStatus()\n    // }, 5000)\n  },\n  beforeDestroy() {\n    // background script\n    if (typeof chrome !== 'undefined' && chrome.runtime) {\n      chrome.runtime.sendMessage({\n        action: 'sidePanelClosed'\n      })\n    }\n\n    // \n    document.removeEventListener('visibilitychange', this.handleVisibilityChange)\n\n    // Chrome\n    this.removeMessageListener()\n    // \n    if (this.connectionCheckTimer) {\n      clearInterval(this.connectionCheckTimer)\n      this.connectionCheckTimer = null\n    }\n    // \n    this.recommendData = []\n    this.expandedItems = []\n  },\n  methods: {\n    handleVisibilityChange() {\n      // background script\n      if (document.hidden) {\n        if (typeof chrome !== 'undefined' && chrome.runtime) {\n          chrome.runtime.sendMessage({\n            action: 'sidePanelClosed'\n          })\n        }\n      } else {\n        this.getPagerInfo().then(res => {\n          this.pageInfo = res\n        })\n        // background script\n        if (typeof chrome !== 'undefined' && chrome.runtime) {\n          chrome.runtime.sendMessage({\n            action: 'sidePanelOpened'\n          })\n        }\n      }\n    },\n\n    isEmptyRole(roleInfo) {\n      const noEquip = this.get_equip_num(roleInfo) === 0\n      let noPet = true\n      for (let pet of roleInfo.pet_info) {\n        if (pet.pet_grade > 100 && pet.is_baobao === '') {\n          noPet = false\n          break\n        }\n        if (pet.pet_grade > 100 && pet.is_baobao === '' && pet.all_skills.length > 4) {\n          noPet = false\n          break\n        }\n      }\n      return noEquip && noPet\n    },\n    get_pet_num(roleInfo) {\n      return roleInfo.pet_info.length + roleInfo.split_pets.length\n    },\n    get_equip_num(roleInfo) {\n      return roleInfo.using_equips.length + roleInfo.not_using_equips.length + roleInfo.split_equips.length\n    },\n    nextPage() {\n      // ChromeAPI\n      this.clickPageButton('next')\n    },\n\n    prevPage() {\n      // ChromeAPI\n      this.clickPageButton('prev')\n    },\n\n    reconnectDevTools() {\n      // \n      this.connectionStatus = '...'\n      this.checkConnectionStatus()\n      this.$notify.info('...')\n    },\n\n    async clickPageButton(direction) {\n      try {\n        // \n        const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })\n\n        if (!activeTab || !activeTab.url.includes('cbg.163.com')) {\n          this.$notify.warning('')\n          return\n        }\n\n        // \n        if (!this.devtoolsConnected) {\n          this.$notify.warning('')\n          return\n        }\n\n        // ChromeAPIJavaScript\n        const result = await chrome.debugger.sendCommand(\n          { tabId: activeTab.id },\n          'Runtime.evaluate',\n          {\n            expression: `\n              (function() {\n                try {\n                  // idpagerdiv\n                  const pagerDiv = document.getElementById('pager')\n                  if (!pagerDiv) {\n                    return 'ERROR:'\n                  }\n                  \n                  let targetButton = null\n                  const isNext = '${direction}' === 'next'\n                  \n                  if (isNext) {\n                    //  - HTML\n                    // 1. \"\"\n                    const allLinks = pagerDiv.querySelectorAll('a')\n                    for (let link of allLinks) {\n                      const text = link.textContent.trim()\n                      if (text === '') {\n                        targetButton = link\n                        break\n                      }\n                    }\n                    \n                    // 2. \"\"goto\n                    if (!targetButton) {\n                      for (let link of allLinks) {\n                        const href = link.getAttribute('href')\n                        const text = link.textContent.trim()\n                        // goto\n                        if (href && href.includes('goto(') && !link.classList.contains('on')) {\n                          // \n                          const currentPageLink = pagerDiv.querySelector('a.on')\n                          if (currentPageLink) {\n                            const currentPageText = currentPageLink.textContent.trim()\n                            const currentPage = parseInt(currentPageText)\n                            const linkPage = parseInt(text)\n                            // \n                            if (!isNaN(linkPage) && linkPage > currentPage) {\n                              targetButton = link\n                              break\n                            }\n                          }\n                        }\n                      }\n                    }\n                  } else {\n                    // \n                    const allLinks = pagerDiv.querySelectorAll('a')\n                    \n                    // 1. \"\"\n                    for (let link of allLinks) {\n                      const text = link.textContent.trim()\n                      if (text === '') {\n                        targetButton = link\n                        break\n                      }\n                    }\n                    \n                    // 2. \"\"goto\n                    if (!targetButton) {\n                      for (let link of allLinks) {\n                        const href = link.getAttribute('href')\n                        const text = link.textContent.trim()\n                        // goto\n                        if (href && href.includes('goto(') && !link.classList.contains('on')) {\n                          // \n                          const currentPageLink = pagerDiv.querySelector('a.on')\n                          if (currentPageLink) {\n                            const currentPageText = currentPageLink.textContent.trim()\n                            const currentPage = parseInt(currentPageText)\n                            const linkPage = parseInt(text)\n                            // \n                            if (!isNaN(linkPage) && linkPage < currentPage) {\n                              targetButton = link\n                              break\n                            }\n                          }\n                        }\n                      }\n                    }\n                  }\n                  \n                  if (!targetButton) {\n                    return 'ERROR:${direction === 'next' ? '' : ''}'\n                  }\n                  \n                  // \n                  if (targetButton.disabled || targetButton.classList.contains('disabled')) {\n                    return 'ERROR:${direction === 'next' ? '' : ''}${direction === 'next' ? '' : ''}'\n                  }\n                  \n                  // \n                  const currentPageLink = pagerDiv.querySelector('a.on')\n                  let currentPageInfo = ''\n                  if (currentPageLink) {\n                    const currentPageText = currentPageLink.textContent.trim()\n                    currentPageInfo = ' (' + currentPageText + ')'\n                  }\n                  \n                  // \n                  targetButton.click()\n                  return 'SUCCESS:${direction === 'next' ? '' : ''}' + currentPageInfo\n                } catch (error) {\n                  return 'ERROR: - ' + error.message\n                }\n              })()\n            `\n          }\n        )\n        this.pageInfo = await this.getPagerInfo()\n        // ChromeAPI\n        if (result && result.result && result.result.value) {\n          const message = result.result.value\n\n          if (message.startsWith('SUCCESS:')) {\n            this.$notify.success(message.substring(8)) // \"SUCCESS:\"\n            console.log(`${direction === 'next' ? '' : ''}`)\n          } else if (message.startsWith('ERROR:')) {\n            this.$notify.warning(message.substring(6)) // \"ERROR:\"\n            console.warn(`${direction === 'next' ? '' : ''}:`, message)\n          } else {\n            this.$notify.error('')\n            console.error(':', result)\n          }\n        } else {\n          this.$notify.error('')\n          console.error(':', result)\n        }\n\n      } catch (error) {\n        console.error(`${direction === 'next' ? '' : ''}:`, error)\n\n        // \n        if (error.message && error.message.includes('Could not establish connection')) {\n          this.devtoolsConnected = false\n          this.connectionStatus = ''\n          this.$notify.error('')\n        } else {\n          this.$notify.error(': ' + error.message)\n        }\n      }\n    },\n\n    async getPagerInfo() {\n      try {\n        // \n        const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })\n\n        if (!activeTab || !activeTab.url.includes('cbg.163.com')) {\n          this.$notify.warning('')\n          return\n        }\n\n        // \n        if (!this.devtoolsConnected) {\n          this.$notify.warning('')\n          return\n        }\n\n        // ChromeAPIJavaScript\n        //pagerDivinnerText `100`100\n        const result = await chrome.debugger.sendCommand(\n          { tabId: activeTab.id },\n          'Runtime.evaluate',\n          {\n            expression: `\n              (function() {\n                let hasPager = false\n                try {\n                  // idpagerdiv\n                  const pagerDiv = document.getElementById('pager')\n                  if (!pagerDiv) {\n                    return 'ERROR:'\n                  }\n                  hasPager = true\n                  // \n                  const currentPageLink = pagerDiv.querySelector('a.on')\n                  let currentPage = 0\n                  if (currentPageLink) {\n                    currentPage = currentPageLink.textContent.trim()\n                  }\n                  \n                  // innerText\"X\"\n                  let total = 0\n                  const innerText = pagerDiv.innerText || pagerDiv.textContent || ''\n                  \n                  // \"\"\"\"\n                  const gongIndex = innerText.indexOf('')\n                  const yeIndex = innerText.indexOf('', gongIndex)\n                  \n                  if (gongIndex !== -1 && yeIndex !== -1) {\n                    const textBetween = innerText.substring(gongIndex + 1, yeIndex).trim()\n                    total = textBetween\n                    console.log('textBetween:', textBetween)\n                    const numberMatch = textBetween.match(/(\\d+)/)\n                    if (numberMatch) {\n                      total = numberMatch[1]\n                    }\n                  }\n                  \n                  // /\n                  const hasPrev = pagerDiv.querySelector('a[href*=\"goto(\"]') && \n                                 pagerDiv.textContent.includes('')\n                  const hasNext = pagerDiv.querySelector('a[href*=\"goto(\"]') && \n                                 pagerDiv.textContent.includes('')\n                  \n                  // return 'SUCCESS:' + currentPage + '' + total + ' (:' + (hasPrev ? '' : '') + ', :' + (hasNext ? '' : '') + ')'\n                  return JSON.stringify({\n                    hasPager: hasPager,\n                    currentPage: currentPage*1,\n                    total: total*1,\n                    hasPrev: hasPrev,\n                    hasNext: hasNext\n                  })\n                } catch (error) {\n                  return 'ERROR: - ' + error.message\n                }\n              })()\n            `\n          }\n        )\n        console.log('resultresultresultresult:', result)\n        // \n        if (result && result.result && result.result.value) {\n          return JSON.parse(result.result.value)\n        } else {\n          return {\n            hasPager: false,\n            currentPage: 0,\n            total: 0,\n            hasPrev: false,\n            hasNext: false\n          }\n        }\n      } catch (error) {\n        console.error(':', error)\n        return {\n          hasPager: false,\n          currentPage: 0,\n          total: 0,\n          hasPrev: false,\n          hasNext: false\n        }\n      }\n    },\n    parserRoleData(data) {\n      const roleInfo = new window.RoleInfoParser(data.large_equip_desc, { equip_level: data.equip_level })\n      return roleInfo.result\n      // return {\n      //   RoleInfoParser: roleInfo,\n      //   roleInfo: roleInfo.result,\n      //   accept_bargain: data.accept_bargain,\n      //   collect_num: data.collect_num,\n      //   dynamic_tags: data.dynamic_tags,\n      //   eid: data.eid,\n      //   highlight: data.highlight,\n      //   is_split_independent_role: data.is_split_independent_role,\n      //   is_split_main_role: data.is_split_main_role,\n      //   large_equip_desc: data.large_equip_desc,\n      //   level: data.level,\n      //   other_info: data.other_info,\n      //   school: data.school,\n      //   seller_nickname: data.seller_nickname,\n      //   server_name: data.server_name,\n      //   serverid: data.serverid,\n      //   price: data.price,\n      //   sum_exp: data.sum_exp,\n      //   create_time: data.create_time,\n      //   update_time: data.create_time,\n      //   all_equip_json: '',\n      //   all_summon_json: '',\n      //   split_price_desc: '',\n      //   pet_price: '',\n      //   equip_price: '',\n      //   base_price: '',\n      //   history_price: '',\n      // }\n    },\n    parseListData(responseDataStr) {\n      //  Request.JSONP.request_map.request_(xxxx) xxxx\n      const match = responseDataStr.match(/Request\\.JSONP\\.request_map\\.request_\\d+\\((.*)\\)/)\n      let templateJSONStr = '{}'\n      if (match) {\n        templateJSONStr = match[1]\n      } else {\n        templateJSONStr = responseDataStr\n      }\n      try {\n        let templateJSON = {}\n        if (typeof templateJSONStr === 'string') {\n          templateJSON = JSON.parse(templateJSONStr)\n        } else {\n          // h5\n          templateJSON = templateJSONStr\n        }\n        return templateJSON\n      } catch (error) {\n        console.error(':', error)\n        return {}\n      }\n    },\n    initMessageListener() {\n      console.log('DevToolsPanel mounted, initializing listener')\n\n      // \n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        // \n        if (window.cbgDevToolsListener) {\n          chrome.runtime.onMessage.removeListener(window.cbgDevToolsListener)\n        }\n\n        // \n        window.cbgDevToolsListener = (request, sender, sendResponse) => {\n          console.log('DevToolsPanel received Chrome message:', request.action)\n          this.handleChromeMessage(request, sender, sendResponse)\n          sendResponse({ success: true })\n        }\n\n        // \n        chrome.runtime.onMessage.addListener(window.cbgDevToolsListener)\n        console.log('Chrome message listener registered for DevToolsPanel')\n      }\n    },\n\n    removeMessageListener() {\n      // Chrome\n      if (typeof chrome !== 'undefined' && chrome.runtime && window.cbgDevToolsListener) {\n        chrome.runtime.onMessage.removeListener(window.cbgDevToolsListener)\n        delete window.cbgDevToolsListener\n        console.log('Chrome message listener removed for DevToolsPanel')\n      }\n    },\n\n    checkConnectionStatus() {\n      // Chrome\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        // ping\n        chrome.runtime.sendMessage({ action: 'ping' }, (response) => {\n          if (chrome.runtime.lastError) {\n            console.log('Chrome extension connection check failed:', chrome.runtime.lastError)\n            this.devtoolsConnected = false\n            this.connectionStatus = ''\n          } else if (response && response.success) {\n            console.log('Chrome extension connection check successful:', response)\n            this.devtoolsConnected = true\n            this.connectionStatus = ''\n          } else {\n            console.log('Chrome extension connection check failed: invalid response')\n            this.devtoolsConnected = false\n            this.connectionStatus = ''\n          }\n        })\n      } else {\n        console.log('Chrome runtime not available')\n        this.devtoolsConnected = false\n        this.connectionStatus = 'Chrome'\n      }\n    },\n    changeRecommendDataStatus({ requestId, status }) {\n      const targetIndex = this.recommendData.findIndex(item => item.requestId === requestId)\n      if (targetIndex !== -1) {\n        // this.recommendData[targetIndex].status = status\n        this.$set(this.recommendData[targetIndex], 'status', status)\n      }\n    },\n    processNewData(dataArray) {\n      // \n      if (dataArray && dataArray.length > 0) {\n        dataArray.forEach(item => {\n          if (item.responseData &&\n            item.url &&\n            item.requestId &&\n            !this.processedRequests.has(item.requestId)) {\n\n            // \n            this.processedRequests.add(item.requestId)\n            console.log(`: ${item.requestId}`)\n\n            // \n            this.$api.spider.parseResponse({\n              url: item.url,\n              response_text: item.responseData\n            }).then(res => {\n              console.log(` ${item.requestId} :`, res)\n              if (res.code === 200) {\n                console.log(` ${item.requestId} :`, res.data)\n                this.changeRecommendDataStatus({ requestId: item.requestId, status: 'completed' })\n              } else {\n                console.error(` ${item.requestId} :`, res.message)\n                this.changeRecommendDataStatus({ requestId: item.requestId, status: 'failed' })\n              }\n            }).catch(error => {\n              console.error(` ${item.requestId} :`, error)\n              // \n              this.processedRequests.delete(item.requestId)\n              this.changeRecommendDataStatus({ requestId: item.requestId, status: 'failed' })\n            })\n          }\n        })\n      }\n    },\n\n    handleChromeMessage(request, sender, sendResponse) {\n      switch (request.action) {\n        case 'addRecommendData':\n          console.log(':', request)\n          // \n          const newData = request.data.map(item => {\n            return {\n              ...item,\n              status: 'parsing'\n            }\n          }) || []\n          if (newData.length > 0) {\n            // \n            this.recommendData.unshift(...newData)\n            \n            // 10\n            const maxLength = 10\n            if (this.recommendData.length > maxLength) {\n              const removedCount = this.recommendData.length - maxLength\n              this.recommendData = this.recommendData.slice(0, maxLength)\n              console.log(`  ${removedCount} `)\n            }\n            \n            this.getPagerInfo().then(res => {\n              this.pageInfo = res\n            })\n            console.log(' :', newData.length, ':', this.recommendData.length)\n            // \n            this.processNewData(newData)\n          }\n          break\n\n        case 'devtoolsConnected':\n          this.devtoolsConnected = true\n          this.connectionStatus = ''\n          this.$notify.success(request.message)\n          break\n\n        case 'showDebuggerWarning':\n          this.devtoolsConnected = false\n          this.connectionStatus = ''\n          this.$notify.warning(request.message)\n          break\n\n        case 'clearRecommendData':\n          this.recommendData = []\n          this.expandedItems = []\n          this.processedRequests.clear()\n          console.log('')\n          break\n      }\n    },\n\n\n    clearData() {\n      this.recommendData = []\n      this.expandedItems = []\n      this.processedRequests.clear() // \n      // background script\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        chrome.runtime.sendMessage({\n          action: 'clearRecommendData'\n        })\n      }\n    },\n\n    // \n    refreshCurrentPage() {\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\n        chrome.runtime.sendMessage({\n          action: 'refreshCurrentPage'\n        }, (response) => {\n          if (chrome.runtime.lastError) {\n            console.error(':', chrome.runtime.lastError)\n            this.$notify.error({\n              title: '',\n              message: ''\n            })\n          } else if (response && response.success) {\n            console.log(':', response.message)\n            this.$notify.success({\n              title: '',\n              message: '...'\n            })\n          } else {\n            console.error(':', response.error)\n            this.$notify.error({\n              title: '',\n              message: response.error || ''\n            })\n          }\n        })\n      } else {\n        this.$notify.error({\n          title: '',\n          message: 'Chrome'\n        })\n      }\n    },\n\n    toggleResponse(index) {\n      const expandedIndex = this.expandedItems.indexOf(index)\n      if (expandedIndex > -1) {\n        this.expandedItems.splice(expandedIndex, 1)\n      } else {\n        this.expandedItems.push(index)\n      }\n    },\n\n    formatTime(timestamp) {\n      if (!timestamp) return ''\n      \n      // \n      const now = new Date()\n      \n      return now.toLocaleTimeString('zh-CN', { \n        hour12: false,\n        hour: '2-digit',\n        minute: '2-digit',\n        second: '2-digit'\n      })\n    },\n\n    checkIfInNewWindow() {\n      // \n      try {\n\n        // 1: chrome.devtools API\n        if (typeof chrome !== 'undefined' && chrome.devtools && chrome.devtools.inspectedWindow) {\n          this.isInNewWindow = false\n          console.log('ChromeSidePanelAPI')\n          return\n        }\n\n        // 2: URL - SidePanel\n        const currentUrl = window.location.href\n        if (currentUrl.includes('chrome-extension://')) {\n          // SidePanel\n          if (currentUrl.includes('panel.html')) {\n            // panel.htmlSidePanel\n            this.isInNewWindow = false\n            console.log('ChromeSidePanelURL')\n            return\n          } else if (currentUrl.includes('panel.html')) {\n            // panel.html\n            this.isInNewWindow = true\n            console.log('URL')\n            return\n          }\n        }\n\n        // 3: \n        if (document.title === '') {\n          // SidePanel\n          if (currentUrl.includes('panel.html')) {\n            this.isInNewWindow = false\n            console.log('ChromeSidePanel+URL')\n            return\n          } else {\n            this.isInNewWindow = true\n            console.log('')\n            return\n          }\n        }\n\n        // 4: iframe\n        if (window.self !== window.top) {\n          this.isInNewWindow = false\n          console.log('ChromeSidePaneliframe')\n          return\n        }\n\n        // 5: parent\n        if (window.parent === window) {\n          // \n          if (currentUrl.includes('panel.html')) {\n            this.isInNewWindow = false\n            console.log('ChromeSidePanelparent+URL')\n          } else {\n            this.isInNewWindow = true\n            console.log('parent')\n          }\n        } else {\n          this.isInNewWindow = false\n          console.log('ChromeSidePanelparent')\n        }\n\n      } catch (error) {\n        console.error(':', error)\n        // \n        this.isInNewWindow = true\n        console.log('')\n      }\n    },\n\n    async openInNewTab() {\n      try {\n        // \n        const extensionUrl = chrome.runtime.getURL('panel.html')\n\n        // chrome.tabs.create\n        await chrome.tabs.create({\n          url: extensionUrl,\n          active: true // \n        })\n\n        this.$notify.success('')\n\n      } catch (error) {\n        console.error(':', error)\n\n        // chrome.tabs.createwindow.open\n        try {\n          const extensionUrl = chrome.runtime.getURL('panel.html')\n          window.open(extensionUrl, '_blank')\n          this.$notify.success('')\n        } catch (fallbackError) {\n          console.error(':', fallbackError)\n          this.$notify.error(': ' + error.message)\n        }\n      }\n    },\n    \n    // \n    async handleEquipPrice(role) {\n      const roleData = this.parserRoleData(role)\n      const { using_equips, not_using_equips, split_equips, basic_info } = roleData\n      const equip_list = [...using_equips, ...not_using_equips, ...split_equips].map((item) => ({ \n        ...item, \n        iType: item.type, \n        cDesc: item.desc, \n        serverid: role.serverid, \n        server_name: role.server_name \n      }))\n      \n      this.valuationDialogTitle = {\n        nickname: basic_info.nickname,\n        school: basic_info.school,\n        server_name: role.server_name,\n        eid: role.eid\n      }\n\n      try {\n        // \n        this.valuationDialogVisible = true\n        this.valuationLoading = true\n        this.valuationResults = []\n        this.valuationTotalValue = 0\n        this.valuationEquipmentList = equip_list\n        \n        // API\n        const response = await this.$api.equipment.batchEquipmentValuation({\n          eid: role.eid,\n          equipment_list: equip_list,\n          strategy: 'fair_value',\n          similarity_threshold: this.batchValuateParams.similarity_threshold,\n          max_anchors: this.batchValuateParams.max_anchors\n        })\n\n        if (response.code === 200) {\n          const data = response.data\n          const results = data.results || []\n          const totalValue = results.reduce((sum, result) => {\n            return sum + (result.estimated_price || 0)\n          }, 0)\n          \n          // \n          this.valuationResults = results\n          this.valuationTotalValue = totalValue\n          this.valuationLoading = false\n        } else {\n          this.$notify.error({\n            title: '',\n            message: response.message || ''\n          })\n          this.closeValuationDialog()\n        }\n      } catch (error) {\n        console.error(':', error)\n        this.$notify.error({\n          title: '',\n          message: ''\n        })\n        this.closeValuationDialog()\n      } finally {\n        this.valuationLoading = false\n      }\n    },\n    \n    // \n    closeValuationDialog() {\n      this.valuationDialogVisible = false\n      this.valuationResults = []\n      this.valuationTotalValue = 0\n      this.valuationEquipmentList = []\n      this.valuationDialogTitle = {}\n    },\n    \n    // \n    handlePetPrice(role) {\n      this.$notify.info({\n        title: '',\n        message: ''\n      })\n    }\n  }\n}\n</script>\n\n<style scoped>\n.panel {\n  box-sizing: border-box;\n  padding: 16px;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;\n  background: #f5f5f5;\n  min-height: 100vh;\n  background: url(~@/../public/assets/images/areabg.webp) repeat-y;\n  width: 960px;\n  margin: 0 auto;\n}\n\n.panel-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n  padding-bottom: 12px;\n  border-bottom: 1px solid #e0e0e0;\n}\n\n.panel-header h3 {\n  margin: 0;\n  color: #333;\n  font-size: 18px;\n}\n\n.connection-status {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n.status-indicator {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.status-dot {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 4px;\n  height: 4px;\n  border-radius: 50%;\n  display: inline-block;\n  transition: all 0.3s ease;\n}\n\n.status-dot.connected {\n  background-color: #52c41a;\n  animation: pulse-green-strong 1.5s infinite;\n}\n\n.status-dot.disconnected {\n  background-color: #faad14;\n  animation: pulse-orange-strong 1s infinite;\n}\n\n/*  */\n@keyframes pulse-green-strong {\n  0% {\n    transform: translate(-50%, -50%) scale(1);\n    box-shadow: 0 0 0 0 rgba(82, 196, 26, 0.7);\n    opacity: 1;\n  }\n\n  50% {\n    transform: translate(-50%, -50%) scale(1.2);\n    box-shadow: 0 0 0 10px rgba(82, 196, 26, 0);\n    opacity: 0.8;\n  }\n\n  100% {\n    transform: translate(-50%, -50%) scale(1);\n    box-shadow: 0 0 0 0 rgba(82, 196, 26, 0.7);\n    opacity: 1;\n  }\n}\n\n/*  */\n@keyframes pulse-orange-strong {\n  0% {\n    transform: translate(-50%, -50%) scale(1);\n    box-shadow: 0 0 0 0 rgba(250, 173, 20, 0.7);\n    opacity: 1;\n  }\n\n  25% {\n    transform: translate(-50%, -50%) scale(1.3);\n    box-shadow: 0 0 0 8px rgba(250, 173, 20, 0.4);\n    opacity: 0.6;\n  }\n\n  50% {\n    transform: translate(-50%, -50%) scale(1.1);\n    box-shadow: 0 0 0 15px rgba(250, 173, 20, 0);\n    opacity: 0.8;\n  }\n\n  75% {\n    transform: translate(-50%, -50%) scale(1.2);\n    box-shadow: 0 0 0 5px rgba(250, 173, 20, 0.2);\n    opacity: 0.7;\n  }\n\n  100% {\n    transform: translate(-50%, -50%) scale(1);\n    box-shadow: 0 0 0 0 rgba(250, 173, 20, 0.7);\n    opacity: 1;\n  }\n}\n\n.status-text {\n  font-size: 12px;\n  color: #666;\n  font-weight: 500;\n}\n\n.mode-indicator {\n  font-size: 10px;\n  padding: 2px 6px;\n  border-radius: 3px;\n  font-weight: bold;\n  margin-left: 8px;\n}\n\n.mode-indicator.sidepanel {\n  background-color: #1890ff;\n  color: white;\n}\n\n.mode-indicator.new-window {\n  background-color: #52c41a;\n  color: white;\n}\n\n.new-window-tip {\n  margin-bottom: 16px;\n  border-radius: 6px;\n}\n\n.new-window-tip p {\n  margin: 4px 0;\n  font-size: 12px;\n  line-height: 1.4;\n}\n\n.sidebar-tip {\n  margin-bottom: 16px;\n  border-radius: 6px;\n}\n\n.sidebar-tip p {\n  margin: 4px 0;\n  font-size: 12px;\n  line-height: 1.4;\n}\n\n.data-section h4 {\n  margin: 0 0 12px 0;\n  color: #666;\n  font-size: 14px;\n}\n\n.empty-state {\n  text-align: center;\n  padding: 40px 20px;\n  color: #999;\n  background: white;\n  border-radius: 4px;\n  border: 1px dashed #ddd;\n}\n\n.r